.MovieInfo {
    position: fixed;
    background: #393e46;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    color: #eeeeee;
    padding: 25px;
    overflow: auto;
}

.close {
    position: absolute;
    top: 25px;
    right: 25px;
    font-size: 35px;
    cursor: pointer;
    transition: .2s;    
}

.close:hover {
    color: #00adb5;
}

.firstBlock{
    width: 100%;
    display: grid;
    grid-template-columns: 300px 1fr;
    grid-column-gap: 90px;
}

.poster {
    width: 100%;
    border-radius: 15px;
    margin-bottom: 15px;
}

.wrapper{
    width: 100%;
    max-width: 1280px;
    margin: 0 auto;
}

h1{
    font-size: 40px;
    padding-right: 35px;
}

.reslease{
    color: #00adb5;
    font-size: 23px;
    margin: 15px 0 8px 0 ;
}

.overview{
    font-size: 20px;
}

.ganre{
    margin-top: 5px;
    color: #d6c211;
}

.production{
    margin-top: 10px;
    font-size: 20px;
}

.word {
    color: #f05454;
    font-weight: normal;
}

.posterWrapper{
    display: flex;
    flex-direction: column;
    align-items: center;
}

a{
    text-decoration: none;
    padding: 18px 100px;
    background: #b02725;
    color: #eeeeee;
    border-radius: 15px;
    transition: .2s;
}

a:hover {
    background: #99110f;
}

.rate > i{
    color: #f2ec30
}

.rate {
    font-size: 22px;
    margin-bottom: 4px;
    margin-bottom: 15px;
}

.rewWrapper{
    width: 100%;
    background-color: #222831;
    height: 600px;
    border-radius: 15px;
}

.rewTitle{
    text-align: center;
    font-size: 38px;
    font-weight: 500;
    margin-top: 25px;
    margin-bottom: 10px;
}

.errMas {
    text-align: center;
    font-size: 46px;
    margin-top: 15px;
    color: #ed1a2f;
}

@media (max-width: 800px) {
    .MovieInfo .firstBlock{
        display: block;
    }
    .MovieInfo .poster{
        width: 85%;
    }
    .MovieInfo a{
        width: 85%;
        text-align: center;
    }

    .MovieInfo h1{
        margin-top: 20px;
    }
    .MovieInfo .overview, .ganre, .reslease, .rate {
        width: 85% !important;
        margin: 10px auto !important;

    }

    .MovieInfo .production {
        margin: 0 auto !important;
        width: 85% !important;
    }

    .title{
        width: 85%;
        margin: 0 auto;
        margin-top: 20px;
    }
}

@media (max-width: 590px) {
    .wrapper {
        padding-top: 40px;
    }
}

@media (max-width: 460px) {
    .MovieInfo .overview, .ganre, .reslease, .rate, .production {
        width: 85% !important;
        margin: 10px auto !important;
        font-size: 16px;
    }

    .MovieInfo .title {
        font-size: 32px;
    }
}