.Navbar {
    width: 100%;
    height: 80px;
    border-bottom: 5px solid #00adb5;
    background: #222831;
}

.wrapper {
    max-width: 90%;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 0 auto;
}

.Navbar .logo{
    width: 60px;
    height: 60px;
}

ul {
    display: flex;
    justify-content: space-between;
    width: 350px;
    padding: 0;
}

li{
    list-style: none;
    text-decoration: none;
    color: #00adb5;
    font-weight: 600;
    font-size: 19px;
    transition: .2s;
    cursor: pointer;
}


li:hover {
    color: #eeeeee;
}

@media (max-width: 840px) {
    ul {
        width: 290px;
    }
}

@media (max-width: 655px) {
    ul {
        width: 230px;
    }
    li{
        font-size: 16px;
    }
    
}

@media (max-width: 550px) {
    .wrapper {
        flex-direction: column;
    }

    .Navbar{
        height: 170px;
        padding: 15px 0;    
    }

    ul{
        width: 260px;
    }
}