.rewWrapper{
    width: 100%;
    background-color: #222831;
    border-radius: 15px;
    padding: 25px;
    height: 500px;
    margin-bottom: 50px;
}

.rewTitle{
    text-align: center;
    font-size: 38px;
    font-weight: 500;
    margin-top: 25px;
    margin-bottom: 10px;
}

.userName{
    font-weight: 500;
    font-size: 25px;
    color: #f05454;
}

.content{
    font-size: 19px;
    margin: 5px 0 10px 0;
}

.contentTime{
    color: #00adb5;
    font-size: 20px;
}

.avatar{
    width: 50px;
    height: 50px;
}

.hide {
    overflow: auto;
    width: 100%;
    height: 100%;
}
section {
    padding: 15px 0;
}

p > i{
    font-size: 20px;
    color: #f2ec30;
}

.rating{
    font-weight: normal;
    color: #eeeeee;
    font-size: 22px;
}

@media (max-width: 460px) {
    .content{
        font-size: 15px;
    }

    .contentTime {
        font-size: 16px;
    }

    .userName {
        font-size: 17px;
    }

    .rating {
        font-size: 17px;
    }

    p > i {
        font-size: 17px;
    }
}